/*Sidebar*/
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.sidebar {
    height: 100vh;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 350px;
    background: #1e1e2d;
    z-index: 100;
    overflow-y: scroll;
    border-radius: 0px 30px 30px 0px;
    word-break: break-word;
}

.sidebar_section {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    word-break: break-word;
}

.sidebar_section h2 {
    color: white;
}

.sidebar-title .check-font{
    font-size: 20px;
}

.sidebar .myButton{
    word-break: break-word;
    text-align: center;
    width: 60%;
    margin: 5px auto;
    color:#a2a3b7;
    /*text-transform: uppercase;*/
    font-family: 'Poppins', sans-serif;
    padding: 9px 27px 9px 27px;
    line-height: 1.5;
    /*background: white;*/
    border-radius: 7px;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
    position: relative;
}

.button-section{
    position: relative;
}

.sidebar .myButton:hover{
    color: white;
    background: #4c4e6f;
    border-radius: 7px;
}



.cat-times{
    position: absolute;
    top:50%;
    -webkit-transform: translateY(-60%);
    -ms-transform: translateY(-60%);
    transform: translateY(-60%);
    right: 80px;
    font-size: 20px;

    cursor: pointer;
    color: #007bff;
}


.sidebar-title{
    margin: 25px auto;
    font-family: 'Poppins', sans-serif;
    position: relative;
    font-size: 24px;
    text-align: center;
    z-index: -1;
    color:#4c4e6f;
}

.sidebar_input{
    padding: 16px 16px 16px 16px;
    margin: 0 auto;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width: 300px;
    font-family: 'Poppins', sans-serif;
    /*border: 1px solid red;*/
    position: relative;
    display: block;
    background: #1e1e2d;
    font-size: 20px;
    color: #4c4e6f;
    line-height: 1.4em;
    z-index: 100;
}

.sidebar_input::-webkit-input-placeholder{
    color: #4c4e6f;
}

.sidebar_input::-moz-placeholder{
    color: #4c4e6f;
}

.sidebar_input:-ms-input-placeholder{
    color: #4c4e6f;
}

.sidebar_input::-ms-input-placeholder{
    color: #4c4e6f;
}

.sidebar_input::placeholder{
    color: #4c4e6f;
}

@media (max-width: 1200px) {
    .sidebar{
        left: -350px;
        top: 60px;
        -webkit-transition: 0.4s;
        -o-transition: 0.4s;
        transition: 0.4s;
        border-radius: 0;
    }

    .sidebar.active{
        height: 100vh;
        display: block;
        position: fixed;
        top: 60px;
        left: 0;
        width: 350px;
        background: #1e1e2d;
        z-index: 100;
        overflow-y: auto;
        border-radius: 0 0px 0px 0;
        word-break: break-word;
        -webkit-transition: 0.4s;
        -o-transition: 0.4s;
        transition: 0.4s;

    }
}


@media(max-width: 930px){
    .sidebar.active{
        width: 100%;
        overflow-y: scroll;
    }
    .button-section{
        width: 300px;
        margin: 0 auto;
        overflow: hidden;
    }
    .cat-times{
        right: 80px;
    }
    .myButton{
        width: 200px
    }
}



@media (max-width: 564px) and (min-width: 0px) {
    .sidebar_input{
        font-size: 19px;
        min-width: 100px;
        max-width: 550px;
    }
    .sidebar-title{
        width: 180px;
    }
}


