.Row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}


footer {
    /*display: flex;*/
    background: #F3F6F9;
    border-radius: 0 0 50px 50px;
    padding: 8px 30px 8px 30px;
    position: relative;
    font-family: 'Poppins', sans-serif;
    line-height: 1.4em;
    /*background: #f5f5f5;*/
    color: #3A3945;
    min-width: 230px;
    max-width: 550px;
    margin: 0 auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 300;
}

footer span {
    display: inline-block;
    margin: 0 10px 0 10px;
    font-family: 'Poppins', sans-serif;
    color: #777;
    line-height: 1.4em;
    -webkit-font-smoothing: antialiased;
    font-weight: 300;
    background: #F3F6F9;
}


button {
    display: inline-block;
    font-family: 'Poppins', sans-serif;
    color: #777;
    line-height: 1.4em;
    -webkit-font-smoothing: antialiased;
    font-weight: 300;
    background: #F3F6F9;
    padding: 2px 10px 2px 10px;

}

.clear:hover {
    padding: 1px 10px 1px 10px;
    border: none;
    text-decoration: underline;
}

button:hover {
    padding: 1px 9px 1px 9px;
    border-radius: 3px;
    border: 1px solid #007bff;
    opacity: 0.5;
}


@media (max-width: 564px) and (min-width: 0px) {
    button{
        font-size: 11px;
        background: transparent;
        padding: 1px 5px 1px 5px;
    }
}