.myflexInput {
    display: flex;
    border-bottom: 1px solid #dddddd;
    background: #F3F6F9;
}

.arrow {
    display: inline-block;
    width: 90px;
    height: 66px;
    background: #F3F6F9;
}

.arrow_active {
    display: inline-block;
    width: 90px;
    height: 66px;
    background: #f5f5f5;
}

.arrow:before {
    content: '';
    cursor: pointer;
    top: 22px;
    left: 33px;
    height: 17px;
    width: 17px;
    border-bottom: 3px solid rgb(77, 77, 77);
    border-left: 3px solid rgb(77, 77, 77);
    opacity: 0.2;
    transform: rotate(315deg);
    display: block;
    position: absolute;
}


.arrow_active:before {
    content: '';
    cursor: pointer;
    top: 22px;
    left: 33px;
    height: 17px;
    width: 17px;
    border-bottom: 3px solid rgb(77, 77, 77);
    border-left: 3px solid rgb(77, 77, 77);
    opacity: 0.9;
    transform: rotate(315deg);
    display: block;
    position: absolute;
}


.arrow:hover:before {
    content: '';
    cursor: pointer;
    top: 22px;
    left: 33px;
    height: 17px;
    width: 17px;
    border-bottom: 3px solid rgb(77, 77, 77);
    border-left: 3px solid rgb(77, 77, 77);
    opacity: 0.6;
    transform: rotate(315deg);
    display: block;
    position: absolute;
}

.input {
    padding: 16px 16px 16px 16px;
    width: 100%;
    border: none;
    background: rgba(0, 0, 0, 0.003);
    position: relative;
    display: inline-block;
    font-size: 24px;
    color: #999;
    line-height: 1.4em;
    margin: 0;
    z-index: 1;
}

.input::-webkit-input-placeholder {
    color: #999;
}

.input:-ms-input-placeholder {
    color: #999;
}

.input::-ms-input-placeholder {
    color: #999;
}

.input::placeholder {
    color: #999;
}

.panel {
    border-radius: 50px;
    padding: 16px 36px 16px 90px;
    border-bottom: 1px solid #dddddd;
    position: relative;
    display: block;
    width: 100%;
    word-break: break-word;
    background: #F3F6F9;
}


.panel span {
    display: inline-block;
    line-height: 1.2em;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;

    font-size: 24px;
    background: #f5f5f5;
}

/*Toggle*/

.container {
    display: block;
    cursor: pointer;
    font-size: 22px;
    background: black;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 50%;
    transform: translatey(-50%);
    left: 23px;
    height: 35px;
    width: 35px;
    border-radius: 50px;
    border: 1px solid #ccc;
    background: rgba(0, 0, 0, 0.003);
}

/* On mouse-over, add a grey background color */


/* When the checkbox is checked, add a blue background */
.container input:checked ~ .checkmark {
    /*background-color: #4d4d4d;*/
    border-color: #007bff;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
    left: 13px;
    top: 7px;
    width: 8px;
    height: 17px;
    border: solid #007bff;
    border-width: 0 2px 2px 0;
    transform: rotate(40deg);
}


/*Footer*/


.addnote {
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    color: #777;
    line-height: 1.4em;
    -webkit-font-smoothing: antialiased;
    font-weight: 300;

}





.panel:hover .times {
    opacity: 0.5;
    transition: 0.2s;
}


.panel:hover .times:hover {
    opacity: 0.9;
}


