/*Dropsdown*/

.dropdown{
    position: absolute;
    top: 50%;
    left: 75px;
    width: 30px;
    /*border: 1px solid black;*/
    /*transform: translateY(-50%);*/
    height: 19px;
    /*z-index: 100;*/
    cursor: pointer;
}

.current-priority{
    position: absolute;
    transform: translateY(-100%);
    top: 50%;
    word-break: normal;
    font-size: 17px;
    cursor: pointer;
    padding: 2px;
    color: #777;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;

}

.dropdown-content{
    display: none;
    cursor: pointer;
    position: absolute;
    background: #f5f5f5;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
    border-radius: 7px;
    top: 15px;
    left: -50%;
    transform: translateX(-10%);
    word-break: normal;
    padding: 15px 20px;


}


.dropdown:hover .dropdown-content{
    display: block;
    z-index: 1000;
}

/*Filtr*/


.filtr {
    display: flex;
    flex-direction: column;
}







