/*Items*/

.items {
    color: #3A3945;
    display: inline-block;
    margin-left: 35px;
}


.items_active {
    display: inline-block;
    margin-left: 35px;
    color: #d9d9d9;
    text-decoration: line-through;
}

.dropdown-content__high:hover{
    outline: 1px solid #777;
    -moz-outline-radius:3px;
}
.dropdown-content__mid:hover{
    outline: 1px solid #777;
    -moz-outline-radius:3px;
}
.dropdown-content__low:hover{
    outline: 1px solid #777;
    -moz-outline-radius:3px;
}

.dropdown-content__high{
    color: #777;
}
.dropdown-content__mid{
    color: #777;
}
.dropdown-content__low{
    color: #777;
}

.dropdown-content div{
    padding: 0 5px 0 7px;
    line-height: 2;
    font-size: 15px;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;

}

.times {
    position: absolute;
    top: 50%;
    font-size: 30px;
    right: 13px;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    color: #007bff;
    opacity: 0;
    cursor: pointer;
}

@media (max-width: 564px) and (min-width: 0px) {
    .current-priority{
        font-size: 15px;
    }
    .dropdown-content div{
        font-size: 15px;
    }
}