@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200&display=swap');

* {
    padding: 0;
    margin: 0;
    border: 0;
}

*, *:before, *:after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

:focus, :active {
    outline: none;
}

a:focus, a:active {
    outline: none;
}

nav, footer, header, aside {
    display: block;
}

html, body {
    background-color: #F3F6F9;
    height: 100%;
    width: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input, button, textarea {
    font-family: inherit;
}


button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a, a:visited {
    text-decoration: none;
    margin: 0;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
    font-size: inherit;
    font-weight: 400;
}



.title-little {
    position: relative;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    margin: 100px 0 0 500px;
    width: 550px;
    font-size: 50px;
    font-weight: 100;
    /*color: rgba(175, 47, 47, 0.15);*/
    color: #3f4254;
}


header {
    margin: 0 auto;
    width: auto;
    padding-top: 100px;

}


.shadow {
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
    margin: 80px auto 40px 500px;
    border-radius: 0 0 30px 30px;
    min-width: 230px;
    max-width: 550px;
    position: relative;
}


main {

    position: relative;
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    line-height: 1.4em;
    background: #f5f5f5;
    color: #4d4d4d;
    margin: 0px auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 300;

}


.myflexInput {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #dddddd;
    background: #F3F6F9;
}

.arrow {
    display: inline-block;
    width: 90px;
    height: 66px;
    background: #F3F6F9;
}

.arrow_active {
    display: inline-block;
    width: 90px;
    height: 66px;
    background: #f5f5f5;
}

.arrow:before {
    content: '';
    cursor: pointer;
    top: 22px;
    left: 33px;
    height: 17px;
    width: 17px;
    border-bottom: 3px solid rgb(77, 77, 77);
    border-left: 3px solid rgb(77, 77, 77);
    opacity: 0.2;
    -webkit-transform: rotate(315deg);
    -ms-transform: rotate(315deg);
    transform: rotate(315deg);
    display: block;
    position: absolute;
}


.arrow_active:before {
    content: '';
    cursor: pointer;
    top: 22px;
    left: 33px;
    height: 17px;
    width: 17px;
    border-bottom: 3px solid rgb(77, 77, 77);
    border-left: 3px solid rgb(77, 77, 77);
    opacity: 0.9;
    -webkit-transform: rotate(315deg);
    -ms-transform: rotate(315deg);
    transform: rotate(315deg);
    display: block;
    position: absolute;
}


.arrow:hover:before {
    content: '';
    cursor: pointer;
    top: 22px;
    left: 33px;
    height: 17px;
    width: 17px;
    border-bottom: 3px solid rgb(77, 77, 77);
    border-left: 3px solid rgb(77, 77, 77);
    opacity: 0.6;
    -webkit-transform: rotate(315deg);
    -ms-transform: rotate(315deg);
    transform: rotate(315deg);
    display: block;
    position: absolute;
}

.input {
    padding: 16px 16px 16px 16px;
    width: 100%;
    border: none;
    background: rgba(0, 0, 0, 0.003);
    position: relative;
    display: inline-block;
    font-size: 24px;
    color: #999;
    line-height: 1.4em;
    margin: 0;
    z-index: 1;
}

.input::-webkit-input-placeholder {
    color: #999;
}

.input::-moz-placeholder {
    color: #999;
}

.input:-ms-input-placeholder {
    color: #999;
}

.input::-ms-input-placeholder {
    color: #999;
}

.input::placeholder {
    color: #999;
}

.panel {
    border-radius: 50px;
    padding: 16px 36px 16px 90px;
    border-bottom: 1px solid #dddddd;
    position: relative;
    display: block;
    width: 100%;
    word-break: break-word;
    background: #F3F6F9;
}


.panel span {
    display: inline-block;
    line-height: 1.2em;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;

    font-size: 24px;
    background: #f5f5f5;
}

/*Toggle*/

.container {
    display: block;
    cursor: pointer;
    font-size: 22px;
    background: black;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 50%;
    -webkit-transform: translatey(-50%);
    -ms-transform: translatey(-50%);
    transform: translatey(-50%);
    left: 23px;
    height: 35px;
    width: 35px;
    border-radius: 50px;
    border: 1px solid #ccc;
    background: rgba(0, 0, 0, 0.003);
}

/* On mouse-over, add a grey background color */


/* When the checkbox is checked, add a blue background */
.container input:checked ~ .checkmark {
    /*background-color: #4d4d4d;*/
    border-color: #007bff;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
    left: 13px;
    top: 7px;
    width: 8px;
    height: 17px;
    border: solid #007bff;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(40deg);
    -ms-transform: rotate(40deg);
    transform: rotate(40deg);
}


/*Footer*/


.addnote {
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    color: #777;
    line-height: 1.4em;
    -webkit-font-smoothing: antialiased;
    font-weight: 300;

}





.panel:hover .times {
    opacity: 0.5;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}


.panel:hover .times:hover {
    opacity: 0.9;
}


@media (max-width: 1200px) and (min-width: 915px){
    .shadow{
        margin: 100px auto;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }

    .shadow.active{
        -webkit-transform: translateX(32%);
        -ms-transform: translateX(32%);
        transform: translateX(32%);
    }



    .title-little{
        margin: 150px auto 0;
        /*border: 1px solid black;*/
        font-size: 40px;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }

    .title-little.active{
        -webkit-transform: translateX(32%);
        -ms-transform: translateX(32%);
        transform: translateX(32%);
    }
}

@media (max-width: 915px){

    .shadow{
        margin: 100px auto;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }
    .shadow.active{
