@media (max-width: 564px) and (min-width: 0px){
    .shadow{
        margin: 40px auto 0;
        min-width: 230px;
        max-width: 550px;
    }

    .input{
        padding: 16px;
        min-width: 230px;
        max-width: 550px;
        font-size: 17px;
    }

    .main{
        min-width: 230px;
        max-width: 550px;
    }

    .title-little {
        margin: 90px auto 0;
        width: 200px;
        font-size: 30px;
        color: #1e1e2d;
    }

    .panel span{
        font-size: 18px;

    }

}

@media (max-width: 300px) and (min-width: 0px){

    .arrow{
        display: none;
    }

}


h2{
    font-size: 50px;
    color: white;
    width: 200px;
    margin: 0 auto;
}


li{
    color: white;

}

ul{
    display: block;
    width: 100px;
    margin: 0 auto;
}

/*Dropsdown*/

.dropdown{
    position: absolute;
    top: 50%;
    left: 75px;
    width: 30px;
    /*border: 1px solid black;*/
    /*transform: translateY(-50%);*/
    height: 19px;
    /*z-index: 100;*/
    cursor: pointer;
}

.current-priority{
    position: absolute;
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
    transform: translateY(-100%);
    top: 50%;
    word-break: normal;
    font-size: 17px;
    cursor: pointer;
    padding: 2px;
    color: #777;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;

}

.dropdown-content{
    display: none;
    cursor: pointer;
    position: absolute;
    background: #f5f5f5;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
    border-radius: 7px;
    top: 15px;
    left: -50%;
    -webkit-transform: translateX(-10%);
    -ms-transform: translateX(-10%);
    transform: translateX(-10%);
    word-break: normal;
    padding: 15px 20px;


}


.dropdown:hover .dropdown-content{
    display: block;
    z-index: 1000;
}

/*Filtr*/


.filtr {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}






