@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200&display=swap');

.navbar{
    position: fixed;
    background: #F3F6F9;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    top: 0;
    left: 0;
    height: 60px;
    width: 100%;
    padding-left: 350px;
    z-index: 100;
    border-radius: 0 0 0px 0px;
}
.navbar__little{
    display: none;
}



/*Navbar Section*/

.navbar__links-section{
    padding: 0 30px 0 0;
    margin: auto 0;
    width: auto;
}

.navbar ul{
    width: inherit;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.navbar ul li{
    font-size: 17px;
    font-weight: bold;
    margin: 0 5px;

}
.check-font{
    font-size: 25px;
    -webkit-transform: rotate(-5deg);
    -ms-transform: rotate(-5deg);
    transform: rotate(-5deg);
}

.form__group .search-font{
    font-size: 10px;
}
/*Logo*/

.navbar__logo{
    margin: auto 0;
    padding: 0 40px 0 0px;

}

.navbar .logo__title{
    font-family: 'Poppins', sans-serif;
    font-size: 30px;
    color: #a2a3b7;
    font-weight: 200;
}

.note{
    color:#007bff;
    font-weight: 500;
    font-size: 35px;
}

/*Navbar section*/



/*search input*/

.form__group {
    position: relative;
    padding: 7px 0 0 0;
    margin-top: 0px;
    width: 250px;
    margin-left: 30px;
    margin-bottom: 10px;

}

.form__field {
    width: 80%;
    border: 0;
    border-bottom: 2px solid #a2a3b7;
    outline: 0;
    font-size: 17px;
    color: #a2a3b7;
    padding: 2px 0;
    background: transparent;
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    -webkit-box-shadow:none;
    box-shadow:none;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

/*.form__field::placeholder {*/
/*     color: transparent;*/
/* }*/



.form__field:focus {
    width: 100%;
    padding-bottom: 2px;
    font-weight: 300;
    border-width: 2px;
    -o-border-image: -o-linear-gradient(left, #007bff,#007bff );
    border-image: -webkit-gradient(linear, left top, right top, from(#007bff),to(#007bff) );
    border-image: linear-gradient(to right, #007bff,#007bff );
    border-image-slice: 1;
}

.form__field:focus-visible{
    width: 100%;
}


/* reset input */

.form__field:required{
    -webkit-box-shadow:none;
    box-shadow:none;

}

.form__field:invalid{
    -webkit-box-shadow:none;
    box-shadow:none;
}


@media (max-width: 1200px) {
    .navbar{
        display: none;
    }
    .navbar__little{
        position: fixed;
        background: #F3F6F9;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        top: 0;
        left: 0;
        height: 60px;
        width: 100%;
        z-index: 100;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }

    .navbar__little-active{
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
        position: fixed;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        top: 0;
        left: 0;
        height: 60px;
        width: 100%;
        z-index: 100;
        background: #1e1e2d;
    }

    /*Burger*/

    .navbar__burger{
        margin: auto 257px auto 40px;
        position: relative;
    }

    .navbar__burger .first_child{
        height: 2px;
        width: 25px;
        background: #a2a3b7;
        margin: 5px 0;
        border-radius: 100px;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }

    .navbar__burger .second_child{
        height: 2px;
        width: 17px;
        background: #a2a3b7;
        margin: 5px 0;
        opacity: 1;
        border-radius: 100px;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }

    .navbar__burger .third_child{
        height: 2px;
        width: 9px;
        background: #a2a3b7;
        margin: 5px 0;
        border-radius: 100px;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }

    .navbar__burger:hover .second_child{
        width: 25px;
    }

    .navbar__burger:hover .third_child{
        width: 25px;
    }

    .first_child.active{
        position: absolute;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    }

    .navbar__burger:hover .first_child.active{
        background: #d5d5d5;
    }

    .second_child.active{
        width: 25px;
        opacity: 0;
    }

    .third_child.active{
        position: absolute;
        top: 0;
        -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
        width:25px;
    }

    .navbar__burger:hover .third_child.active{
        background: #d5d5d5;
    }


    /*Logo*/

    .navbar__logo{
        margin: auto 0;
        padding: 0 40px 5px 0;
    }

    .logo__title{
        font-family: 'Poppins', sans-serif;
        font-size: 30px;
        color: #a2a3b7;
        font-weight: 200;
    }

    .note{
        color:#007bff;
        font-weight: 500;
        font-size: 35px;
    }

}

@media (max-width: 821px) {
    .navbar__burger{
        margin: auto auto auto 40px;
    }

}

@media (max-width: 564px) and (min-width: 0px){
    .navbar__burger{
        margin: auto auto auto 20px;
    }

    .form__group{
        width: 200px;
    }
    .navbar__logo{
        display: none;
    }
}
