@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200&display=swap');


.homepage{
    width: 700px;
    height: auto;
    margin: 100px 0 0 350px;
    /*border: 1px solid black;*/
}



.title {
    position: relative;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    margin: 0 auto 0;
    font-size: 60px;
    font-weight: 500;
    /*color: rgba(175, 47, 47, 0.15);*/
    color: #3699ff;
}

.pic-1{
    width: 400px;
    height: 250px;
    display: block;
    margin: 0px auto 0;
    border-radius: 20px;
    -webkit-box-shadow: 0px 1px 20px -1px rgba(0,0,0,0.64);
    box-shadow: 0px 1px 20px -1px rgba(0,0,0,0.64);
}

.home-text{
    width: 90%;
    height: 100%;
    margin: auto 30px auto 30px;
    text-align: justify;

}

.home-text span{
    color: #3A3945;
    text-align: justify;
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    font-size: 15px;
    line-height: 1.6;
}


.home-flex{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 180px 0 180px 20px;
}

@media (max-width: 1200px){
    .homepage{
        margin: 100px auto 0;
    }
}

@media (max-width: 712px){
    .homepage{
        width: 100%;
    }
    .home-flex{
        margin: 30px auto 100px;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        width: 400px;
    }

    .home-text{
        margin: 30px auto 30px;
        width: 300px;
    }

    .pic-1{
        margin: 30px 0 30px;
    }
}

@media (max-width: 420px) {
    .title{
        font-size: 40px;
        width: auto;
        text-align: center;
    }
    .pic-1 {
        width: 260px;
        height: 160px;
    }
    .home-flex{
        margin: 50px auto 100px;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        width: 250px;
    }
    .home-text span{
        font-size: 13px;
    }
    .home-text{
        margin: auto;
        width: 250px;
    }

}






